#HomeBody{
    align-self:flex-start;
    width: 100%;
    height: auto;
}
#HomeBody>header{
    background-color: rgb(20, 33,65);
    background-image: linear-gradient(90deg,rgb(20, 33,65),#5c9fec);
    background-position-x: 400px;
    background-repeat: no-repeat;
    height: 60px;
    display: flex;
    justify-content: right;
    align-items: center;
    position:relative;
    border-bottom: 2px solid white;
    gap: 20px;
}
#HomeBody>header>img{
    position:absolute;
    left:30px;
    height:80%;
    /* size: 10%; */
}
#HomeBody>header>h2{
    position:absolute;
    left:90px;
    /* height:100%; */
    /* size: 10%; */
    color:hsl(212, 33%, 92%);
}
#HomeBody>header>input:focus{
    border: 2px solid #48efbd;
}
#HomeBody>header>input{
    width: 400px;
    height: 35px;
    border-radius: 18px;
    outline: none;
    border: none;
    transition: border 0.2s;
    padding-left: 20px;
}
#headerAccount{
    padding-right: 20px;
    padding-left: 20px;
    color:white;

    display: flex;
    justify-content: center;
    align-items: center;
    
    width: auto;
    max-width: 200px;
    gap: 30px;
    background-color: hsl(212, 33%, 22%);
    border-left: 2px solid hsl(183, 43%, 57%);
    height: 100%;
}
#headerAccount>a{
    color:white;
}
#headerAccount>a>img{
    height:30px;
    width: 30px;
}